/* global angular*/
import { Preference } from './constants';

/**
 * @name FXNavigationToController
 * @ngkey FXNavigationToController
 * @type {object}
 * @description
 * FX navigation to controller.
 */

export default function FXNavigationToController(
  widgetInstance,
  bbIntents
) {
  const $ctrl = this;

  /**
   * @name routeNavigateTo
   * @type {Number} routeNavigateTo
   * @description
   * Route navigate to
   */
  const routeNavigateTo =
    widgetInstance.getPreference(Preference.CURRENCYPAIRS_TABLE);

  /**
   * @description
   * A set of intents that the FX navigation back controller uses or handles.
   *
   * @name intents
   * @type {Object}
   * @inner
   */
  const intents = {
    navigateTo: bbIntents.create(routeNavigateTo),
  };

  /**
   * @name navigateTo
   *
   * @description
   * Navigate to currency pairs table
   *
   * @type {function}
   */
  const navigateTo = () => {
    intents.navigateTo();
  };

  Object.assign($ctrl, {
    navigateTo,
  });

  return $ctrl;
}



