/**
 * @module widget-fx-navigation-back-ng
 *
 * @description
 * FX navigation back widget.
 *
 *
 * @example
 * <div data-ng-controller="FXNavigationToController as $ctrl">
 *   <button
 *     class="btn btn-link ml-2"
 *     aria-label="{{ back | i18n }}"
 *     data-role="navigate-back"
 *     data-ng-click="$ctrl.navigateTo();$event.stopPropagation();"
 *   >
 *     <i class="fas fa-chevron-left pr-2"></i>
 *     <span data-i18n-key="back"></span>
 *   </button>
 * </div>
 */
import angular from 'vendor-bb-angular';
import extendHooks from 'lib-bb-widget-extension-ng';
import widgetModuleKey, { widgetKey } from 'lib-bb-widget-ng';
import bbIntentModuleKey, { bbIntentKey } from 'lib-bb-intent-ng';
import Controller from './controller';
import defaultHooks from './default-hooks';

const hooksKey = 'widget-fx-rate-table-ng:hooks';

/**
 * @name default
 * @type {string}
 * @description Angular module name
 */
export default angular
  .module('widget-fx-navigation-back-ng', [
    widgetModuleKey,
    bbIntentModuleKey,
  ])

  .factory(hooksKey, extendHooks(defaultHooks))
  .controller('FXNavigationToController', [
    widgetKey,
    bbIntentKey,
    /* into */
    Controller,
  ])
  .name;



